.flex-column {
    display: flex;
    flex-direction: column;
    gap: 15px;
    max-width: 600px;
    margin: auto;
}

.form-group {
    flex: 1;
    display: flex;
    flex-direction: column;
}

/* Labels */
label {
    font-weight: bold;
    margin-bottom: 5px;
}

/* Input and Textarea Styling */
.input-container {
    position: relative;
    display: flex;
    align-items: center;
}
.icon {
    position: absolute;
    left: 10px;
    width: 18px;
    height: 18px;
    color: #666;
}
input, textarea {
    padding: 10px 10px 10px 35px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background: #f3f4f6;
    font-size: 14px;
}

input {
    width: 100%;
    padding: 10px 10px 10px 35px;
}

textarea {
    padding: 10px 10px 10px 15px;
}

/* Button Styling */
button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
    padding: 12px;
    background: #4a90e2;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.2s;

    &:hover {
        background: #357ab8;
    }
    &:disabled {
        background: #ccc;
        cursor: not-allowed;
    }
}

/* Loading Icon */
.loading-icon {
    display: none;
    width: 20px;
    height: 20px;
    animation: spin 1s linear infinite;
}
@keyframes spin {
    100% { transform: rotate(360deg); }
}

/* Status Message */
#status-message {
    text-align: center;
    font-weight: bold;
    margin-top: 10px;
}

/* Hide reCAPTCHA badge */
.grecaptcha-badge { visibility: hidden; }